@keyframes rise {
  0% {
    padding-top: 100%;
  }

  60% {
    padding-top: 7%;
  }

  80% {
    opacity: .5;
    border-bottom: 0 solid #fff;
    padding-top: 7%;
  }

  100% {
    opacity: 1;
    border-bottom: .1em solid #fff;
    padding-top: 7%;
  }
}

.rise-text {
  opacity: 0;
  text-shadow: 2px 2px 5px #fff;
  border-bottom: 0;
  animation-name: rise;
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(0, .25, .55, 1);
  animation-iteration-count: 1;
  animation-delay: .25s;
  animation-fill-mode: forwards;
  position: relative;
}

.rise-text:hover {
  color: #ff6b08;
}

.reveal-text, .reveal-text:after {
  animation-duration: 1s;
  animation-timing-function: cubic-bezier(0, .1, 1, 0);
  animation-iteration-count: 1;
  animation-delay: .25s;
  animation-fill-mode: both;
}

.reveal-text {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  text-shadow: 2px 2px 5px #fff;
  animation-name: reveal-text;
  position: relative;
}

.reveal-text:after {
  content: "";
  transform-origin: 0;
  pointer-events: none;
  background-color: #ff6b08;
  animation-name: revealer-text;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

@keyframes reveal-text {
  from {
    -webkit-clip-path: inset(0 100% 0 0);
    clip-path: inset(0 100% 0 0);
  }

  to {
    -webkit-clip-path: inset(0);
    clip-path: inset(0);
  }
}

@keyframes revealer-text {
  0%, 50% {
    transform-origin: 0;
  }

  60%, 100% {
    transform-origin: 100%;
  }

  50% {
    transform: scaleX(1);
  }

  60% {
    transform: scaleX(1);
  }

  100% {
    transform: scaleX(0);
  }
}

