.container-notifix {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.rectangle {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  width: 50px;
  height: 50px;
  background: radial-gradient(
    circle,
    rgba(255, 103, 1, 0.8) 0%,
    rgba(255, 107, 8, 0.9) 100%
  );
  box-shadow: 0 20px 20px -15px rgba(255, 250, 250, 0.8);
  transform: scale(0);
  border-radius: 50%;
  color: $textColorOnDark;
  opacity: 0;
  overflow: hidden;
  animation: scale-in 0.3s ease-out forwards,
    expand 0.35s 0.25s ease-out forwards, fade-out-left 1s 3s ease-out forwards;
}

.notification-icon {
  width: 50px;
  height: 50px;
  margin: 5px 10px 5px 5px;
  animation: circle 2.5s cubic-bezier(0, 0.2, 0.8, 1);
}

@keyframes circle {
  0%,
  100% {
    animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
  }

  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(720deg);
    animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
  }

  100% {
    transform: rotateY(1800deg);
  }
}
.notification-text {
  display: flex;
  align-items: center;
  padding: 0 12px;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 500;
  animation: fade-in 0.65s ease-in forwards;
}

@keyframes scale-in {
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes expand {
  50% {
    width: 300px;
    border-radius: 6px;
  }

  100% {
    width: 250px;
    border-radius: 4px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 3px 3px -1px rgba(0, 0, 0, 0.12);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.8;
  }
}
@keyframes fade-out-left {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }
}
